@charset "UTF-8";
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.9);
  padding: 0.625em 0;
}
.header__brand {
  font-family: var(--font-kufication);
}
.header__brand a {
  background: -webkit-linear-gradient(45deg, var(--primary), var(--light));
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.header__navbar {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  text-align: center;
  background: var(--dark);
  transition: clip-path 600ms;
  transition-timing-function: cubic-bezier(0.6, -0.28, 0.735, 0.045);
  clip-path: circle(0% at 90% 10%);
}
.header__navbar ul li {
  font-family: var(--font-galaxus);
}
.header__navbar ul li:not(:last-child) {
  margin-bottom: 0.9375em;
}
.header__navbar ul li a {
  font-size: 2.25em;
  padding-left: 0.25em;
  letter-spacing: 0.3125em;
  transition: color 200ms;
  transition-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715);
}
.header__navbar ul li a:hover {
  color: var(--light) !important;
}
.header__navbar.function {
  clip-path: circle(140% at 80% 10%);
}
.header__feature {
  margin-top: 3.4375em;
  display: flex;
  align-items: center;
  justify-content: center;
}
.header__feature-wrapper {
  width: 3.75em;
  height: 3.75em;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 3.125em;
  background: linear-gradient(45deg, var(--success), var(--primary));
  cursor: pointer;
}
.header__feature-wrapper svg {
  font-size: 1.5625em;
  color: var(--light);
  transition: color 200ms;
  transition-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715);
}
.header__feature-wrapper:hover svg {
  color: var(--warning);
}
.header__feature-wrapper:not(:last-child) {
  margin-right: 1.625em;
}
.header__feature-wrapper:hover {
  background: linear-gradient(260deg, var(--success), var(--primary));
}
.header__hamburger {
  z-index: 1000;
}
.header__menu {
  background-color: transparent;
  border: none;
  cursor: pointer;
  display: flex;
  padding: 0;
  outline: none;
}
.header__line {
  fill: none;
  stroke: #fff;
  stroke-width: 3;
  transition: stroke-dasharray 600ms cubic-bezier(0.4, 0, 0.2, 1), stroke-dashoffset 600ms cubic-bezier(0.4, 0, 0.2, 1);
}
.header__line1 {
  stroke-dasharray: 60 207;
  stroke-width: 3;
}
.header__line2 {
  stroke-dasharray: 60 60;
  stroke-width: 3;
}
.header__line3 {
  stroke-dasharray: 60 207;
  stroke-width: 3;
}
.opened .header__line1 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134;
  stroke-width: 3;
}
.opened .header__line2 {
  stroke-dasharray: 1 60;
  stroke-dashoffset: -30;
  stroke-width: 3;
}
.opened .header__line3 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134;
  stroke-width: 3;
}

/* CUSTOM CURSOR */
.cursor {
  position: fixed;
  top: -40px;
  left: -40px;
  pointer-events: none;
  z-index: 9999;
  mix-blend-mode: difference;
}

.cursor:before {
  content: "";
  width: 80px;
  height: 80px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background: white;
  border: 1px solid white;
  transition: all ease 0.15s;
  transform: scale(0.15);
}

.cursor.light:before {
  transform: scale(1);
  background: transparent;
}

.cursor.drag {
  mix-blend-mode: unset;
}

.cursor.drag:before {
  transform: scale(1);
  background: #45cac8;
  border-color: transparent;
}

.cursor.drag:before {
  content: "";
  font-family: "LineIcons";
  font-size: 20px;
  color: #fff;
}

:root {
  --font-signature: "Signature";
  --font-spotyfied: "Spotyfied";
  --font-galaxus: "Galaxus";
  --font-mangaka: "Mangaka";
  --font-kufication: "Kufication";
  --font-memesique: "Memesique";
  --font-titillium-regular: "Titillium Regular";
  --font-titillium-light: "Titillium Light";
  --font-titillium-light-italic: "Titillium Light Italic";
  --font-titillium-bold: "Titillium Bold";
  --font-titillium-semibold: "Titillium Semi Bold";
  --primary: #0d6efd;
  --secondary: #6c757d;
  --success: #198754;
  --info: #0dcaf0;
  --warning: #ffc107;
  --danger: #dc3545;
  --light: #f8f9fa;
  --dark: #212529;
  --primary-background: linear-gradient(225deg, #ffd89b 10%, #19547b 70%);
  --secondary-background: linear-gradient(45deg, #ffd89b 10%, #19547b 70%);
  --focus-background: linear-gradient(45deg, #ffd89b 10%, #dc3545 70%);
  --btn-hover-boxshadow: rgba(0, 0, 0, 0.099) 6px 8px 8px 0px,
    rgba(0, 0, 0, 0.099) 6px 8px 8px 0px, rgba(0, 0, 0, 0.099) -6px 8px 8px 0px,
    rgba(0, 0, 0, 0.099) -6px 8px 8px 0px;
  --btn-focus-boxshadow: rgba(0, 0, 0, 0.099) 0px 8px 8px 0px,
    rgba(0, 0, 0, 0.099) 0px 8px 8px 0px;
  --card-boxshadow: rgba(0, 0, 0, 0.4) 0px 30px 90px;
  --box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}

::-webkit-scrollbar {
  display: none;
}

::-webkit-scrollbar-thumb {
  display: none;
}

::-webkit-scrollbar-track {
  display: none;
}

*::selection {
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-drag: none;
}

* {
  outline: none;
  border: none;
}

*::before,
*::after {
  text-decoration: none;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

